header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*----------------CTA---------*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1rem;
    justify-content: center;

}
/*----------------Header Socials---------*/

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;

}
.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background:var(--color-primary) ;

}

/*--------------Me-------------------*/

.me{
    width: 22rem;
    height: 60rem;
    position: absolute;
    left:calc(50% - 11rem);
    margin-top: 2rem;
    overflow: hidden;

}

/*--------------Scroll down-------------------*/

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
/*--------------Media queries (medium devices)---------*/

@media screen and (max-width:1024px) {
 .header{
    height: 68vh;
 }
}


/*--------------Media queries (small devices)---------*/

@media screen and (max-width:600px) {
  .header{
    height: 100vh;
  }  

  .header__socials,
  .scroll__down{
    display: none;
  }
}
